.form-control {
    border: 1px solid var(--light-gray);
    border-radius: 6px;
    line-height: 1.5;
    padding: 0.1em;
    padding: 0.5em 1em;
    width:auto;
}

.form-control-lg {
    width: 100%;
}

.form-control:focus {
    outline: none;
    border: 2px solid var(--primary);
}